"use client"

export default function ChatList({
  chats,
  selectedChatId,
  onSelectChat,
}: {
  chats: Array<{
    id: string
    name: string
    lastMessage: string
    avatar: string
    unread: number
    timestamp: string
  }>
  selectedChatId: string | null
  onSelectChat: (id: string) => void
}) {
  return (
    <div className="flex-1 overflow-y-auto">
      {chats.length === 0 ? (
        <div className="flex items-center justify-center h-full" style={{ color: "var(--color-text-tertiary)" }}>
          <div className="text-center">
            <span className="text-4xl mb-2 block">üîç</span>
            <p>No conversations found</p>
          </div>
        </div>
      ) : (
        <div className="space-y-1 p-2">
          {chats.map((chat) => (
            <button
              key={chat.id}
              onClick={() => onSelectChat(chat.id)}
              className="w-full p-3 rounded-lg transition flex items-center gap-3 hover:bg-opacity-80"
              style={{
                backgroundColor:
                  selectedChatId === chat.id ? "rgba(var(--color-primary-rgb, 124, 58, 237), 0.2)" : undefined,
                border: selectedChatId === chat.id ? "1px solid var(--color-primary)" : undefined,
                color: selectedChatId === chat.id ? "var(--color-text-primary)" : undefined,
              }}
              onMouseEnter={(e) => {
                if (selectedChatId !== chat.id) {
                  e.currentTarget.style.backgroundColor = "var(--color-bg-primary)"
                }
              }}
              onMouseLeave={(e) => {
                if (selectedChatId !== chat.id) {
                  e.currentTarget.style.backgroundColor = ""
                }
              }}
            >
              <div
                className="flex-shrink-0 w-12 h-12 bg-lg rounded-full flex items-center justify-center text-xl"
                style={{ backgroundColor: "var(--color-bg-tertiary)" }}
              >
                {chat.avatar}
              </div>

              <div className="flex-1 min-w-0 text-left">
                <div className="flex items-center justify-between gap-2">
                  <h3 className="text-sm font-semibold truncate" style={{ color: "var(--color-text-primary)" }}>
                    {chat.name}
                  </h3>
                  <span className="text-xs flex-shrink-0" style={{ color: "var(--color-text-tertiary)" }}>
                    {chat.timestamp}
                  </span>
                </div>
                <p className="text-xs truncate" style={{ color: "var(--color-text-tertiary)" }}>
                  {chat.lastMessage}
                </p>
              </div>

              {chat.unread > 0 && (
                <div
                  className="flex-shrink-0 text-white text-xs font-bold w-5 h-5 rounded-full flex items-center justify-center"
                  style={{ backgroundColor: "var(--color-primary)" }}
                >
                  {chat.unread}
                </div>
              )}
            </button>
          ))}
        </div>
      )}
    </div>
  )
}
