"use client"

import { useRouter } from "next/navigation"

export default function UserProfile() {
  const router = useRouter()

  const handleLogout = () => {
    localStorage.removeItem("authToken")
    router.push("/login")
  }

  return (
    <div className="border-t p-4" style={{ borderColor: "var(--color-bg-tertiary)" }}>
      <button
        onClick={handleLogout}
        className="w-full flex items-center gap-3 p-3 hover:bg-opacity-80 rounded-lg transition"
        style={{
          backgroundColor: "transparent",
        }}
      >
        <div
          className="w-10 h-10 rounded-full flex items-center justify-center text-white font-semibold"
          style={{ backgroundImage: "linear-gradient(135deg, var(--color-primary), var(--color-secondary))" }}
        >
          JD
        </div>

        <div className="flex-1 text-left">
          <p className="text-sm font-semibold" style={{ color: "var(--color-text-primary)" }}>
            John Doe
          </p>
          <p className="text-xs" style={{ color: "var(--color-text-tertiary)" }}>
            john@example.com
          </p>
        </div>

        <span style={{ color: "var(--color-text-tertiary)" }}>‚åÑ</span>
      </button>

      <button
        onClick={handleLogout}
        className="w-full mt-2 px-4 py-2 rounded-lg transition text-sm font-medium"
        style={{
          color: "var(--color-error)",
        }}
        onMouseEnter={(e) => {
          e.currentTarget.style.backgroundColor = "rgba(239, 68, 68, 0.1)"
        }}
        onMouseLeave={(e) => {
          e.currentTarget.style.backgroundColor = "transparent"
        }}
      >
        Logout
      </button>
    </div>
  )
}
